import { Component, OnInit } from "@angular/core";
import { SharedComponents } from "src/app/shared.components";
import { ITransaction } from "src/interfaces/i.transaction.interface";
import { AlertService } from "src/services/alert.service";
import { SectorSaleService } from "src/services/sector.sale.service";
 


@Component({
  selector: 'app-create-sector.-sale',
  templateUrl: './create.sector.sale.component.html',
  styles: [
  ]
})
export class CreateSectorSaleComponent extends SharedComponents implements OnInit {
  titleResourcesKey = "createANewSectorSale";

  constructor(public service: SectorSaleService) { super() }

  ngOnInit(): void {
    this.service.getItems();
    this.service.seo.updateSEO(this.titleResourcesKey);
    this.service.initForm();
  }


  /**
    * Create New Item
    */
  onSubmit() {

    if (this.service.form.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.service.isFormSubmitError = false;
    this.allSubscriptions.push(
      this.service.http.createNewSectorSale<ITransaction>(this.service.form.getRawValue())
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          if (res.isSuccess) {
            this.service.form.reset();

            this.service.isRequestingToServer = false;

            this.service.form.enable();
            this.service.disapledControls();
            this.service.sweetAlertService.confirm(this.service.u.r.doYouWantEditCreatedItem, this.service.u.r.goToEdit, this.service.u.r.createNew).then(() => {
              this.service.router.navigate(['/sectorSales', 'edit', res.result._id])
            })
          }
        }, error => {
          this.service.form.enable();
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }



}//End Class
