import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { SharedComponents } from "src/app/shared.components";
import { ITransaction } from "src/interfaces/i.transaction.interface";
import { AlertService } from "src/services/alert.service";
import { SectorSaleService } from "src/services/sector.sale.service";


@Component({
  selector: 'app-edit-tinancial-transfer',
  templateUrl: '../create-sector-sales/create.sector.sale.component.html',
  styles: [
  ]
})
export class EditSectorSaleComponent extends SharedComponents implements OnInit {
  titleResourcesKey = "editSectorSale";
  constructor(public service: SectorSaleService, private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.service.initForm();
    this.service.form.disable();
    this.service.seo.updateSEO(this.titleResourcesKey);
    this.service.getItems();
    this.service.form.controls._id.setValue(this.activateRouter.snapshot.params.targetId);
    this.getSectorSaleToUpdate();
  }


  /**
   * Get Car Mark To Update
    */
  getSectorSaleToUpdate(): void {
    this.service.http.getSectorSaleToUpdate<ITransaction>(this.service.form.controls._id.value)
      .subscribe(res => {
        if (res.isSuccess) {
          this.service.form.setValue(res.result);
          this.service.form.enable();
          this.service.disapledControls();
        }
        else {
          AlertService.showResponseMessage(res);
        }
      }, error => {
        AlertService.canRequestError(error);
      });
  }


  /**
    * Update  
    */
  onSubmit() {
    if (this.service.form.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.service.isFormSubmitError = false;
    this.service.form.disable();

    let values = this.service.form.getRawValue();
    this.allSubscriptions.push(
      this.service.http.updateSectorSale<ITransaction>(values).subscribe(res => {
        this.service.isRequestingToServer = false;
        AlertService.showResponseMessage(res);
        //Update Values Value From Response Data
        values = { ...values, ...res.result };
        this.service.form.setValue(values);
        this.service.form.enable();
        this.service.disapledControls();
      }, error => {
        this.service.form.enable();
        this.service.disapledControls();
        this.service.isRequestingToServer = false;
        AlertService.canRequestError(error);
      }));
  }


}//End Class
