import { SharedComponents } from "src/app/shared.components";
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AlertService } from "src/services/alert.service";
import { ITransaction } from "src/interfaces/i.transaction.interface";
import { SectorSaleService } from "src/services/sector.sale.service";

@Component({
  selector: 'app-details-tinancial-transfer',
  templateUrl: './details.sector.sale.component.html',
  styles: [
    `
.mt-container    {
      height: auto;
    }
    .no-borader>tbody>tr>td{
      border:none !important
    }

    h6{
      display: inline-block;
      color:#1b55e2;
      font-weight: 700;
    font-size: 13px;
    letter-spacing: 1px;
    text-transform: uppercase;
    }

    h6:after{
      content:':'
    }
    `
  ]
})
export class DetailsSectorSaleComponent extends SharedComponents implements OnInit {
  sectorSale: ITransaction = {} as ITransaction;

  constructor(public service: SectorSaleService, private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.service.seo.updateSEO("sectorSalerDetils");
    //Get Detail From The Serer
    this.getSectorSaleDetails();
  }

  /**
   * Get Details
   */
  getSectorSaleDetails() {
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.getSectorSaleDetails<ITransaction>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            this.sectorSale = res.result;
          } else {
            AlertService.showResponseMessage(res);
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }
}//End Class
