import { SharedComponents } from "src/app/shared.components";
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AlertService } from "src/services/alert.service";
import { SectorSaleService } from "src/services/sector.sale.service";
import { ITransaction } from "src/interfaces/i.transaction.interface";

@Component({
  selector: 'app-delete-sector.-sale',
  templateUrl: './delete.sector.sale.component.html',
  styles: [
  ]
})
export class DeleteSectorSaleComponent extends SharedComponents implements OnInit {


  constructor(public service: SectorSaleService, private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.service.initDeletingForm();
    this.service.form.controls._id.setValue(this.activateRouter.snapshot.params.targetId);

    //Get From The Serer
    this.getSectorSaleToDelete();
  }

  /**
   * Get Car Mark From Server To Delete
   */
  getSectorSaleToDelete() {
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.getSectorSaleToDelete<ITransaction>(this.activateRouter.snapshot.params.targetId)
        .subscribe(res => {
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            this.service.form.controls.refranceNumber.setValue(res.result.refranceNumber);
            this.service.form.controls.clinetName.setValue(res.result.clinetName);
          } else {
            AlertService.showResponseMessage(res);
            this.service.closeModel();
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }

  /**
   * Delete Item
   */
  onSubmit() {
    if (this.service.form.invalid) {
      this.service.isFormSubmitError = true;
      return;
    }
    this.service.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.service.http.deleteSectorSale<ITransaction>(this.service.form.controls._id.value)
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.service.isRequestingToServer = false;
          if (res.isSuccess) {
            //Pass The New Item To Page
            this.service.onDeletedItem.emit(this.activateRouter.snapshot.params.targetId);
            //Back To Component
            this.service.closeModel();
          }
        }, error => {
          this.service.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }


}//End Class
