import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { SectorSalesComponent } from 'src/app/sector-sales/sector.sales.component';
import { DeleteSectorSaleComponent } from 'src/app/sector-sales/delete-sector-sales/delete.sector.sale.component';
import { CreateSectorSaleComponent } from 'src/app/sector-sales/create-sector-sales/create.sector.sale.component';
import { EditSectorSaleComponent } from 'src/app/sector-sales/edit-sector-sales/edit.sector.sale.component';
import { DetailsSectorSaleComponent } from "src/app/sector-sales/details-sector-sale/details.sector.sale.component";

 
const ROUTS: Routes = [
    {
        path: '', component: SectorSalesComponent,
        children: [
            {path: "delete/:targetId",component: DeleteSectorSaleComponent} ,
        ]
    },
    { path: 'create', component: CreateSectorSaleComponent },
    { path: 'edit/:targetId', component: EditSectorSaleComponent  },
    { path: 'details/:targetId', component: DetailsSectorSaleComponent  },
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class SectorSalesRoutingModule {

}

