import { EventEmitter, Injectable } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { carSizesArray } from "src/consts/car.sizes.const";
import { CarSizesEnum } from "src/enums/car.sizes.enum";
import { ICity } from "src/interfaces/i.city.interface";
import { IGeneralSetting } from "src/interfaces/i.general.setting.interface";
import { IState } from "src/interfaces/i.state.interface";
import { IUser } from "src/interfaces/i.user.interface";
import { AlertService } from "./alert.service";
import { CurrentUserService } from "./authentcation/current.user.service";
import { CustomValidatorsService } from "./custom.validators.service";
import { HttpService } from "./http/http.service";
import { SEOService } from "./seo.service";
import { SweetAlertService } from "./sweet.alert.service";

@Injectable()
export class    SectorSaleService {

    clinets: IUser[] = [];

    freightCases: IGeneralSetting[];
    buySources: IGeneralSetting[];
    states: IState[];
    cities: ICity[];
    ports: IGeneralSetting[];
    carSizes = carSizesArray;
    navigationalWayes: IGeneralSetting[];
    containers: IGeneralSetting[];
    isFormSubmitError: boolean = false;
    isRequestingToServer: boolean = false;

    form: FormGroup = new FormGroup({});
    onDeletedItem: EventEmitter<string> = new EventEmitter<string>();

    constructor(public u: CurrentUserService, public seo: SEOService, public router: Router, public http: HttpService, public sweetAlertService: SweetAlertService) { }
    /**
     * Init Create And Update Form
     */
    initForm() {
        this.form = new FormGroup({
            _id: new FormControl(),
            userId: new FormControl(null, [Validators.required]),
            notes: new FormControl(null),
            carMark: new FormControl(null, [Validators.required]),
            buySourceId: new FormControl(null, [Validators.required]),
            freightStateId: new FormControl(null, [Validators.required]),
            cityId: new FormControl(null, [Validators.required]),
            carSizeId: new FormControl(null, [Validators.required]),
            freightPortId: new FormControl(null, [Validators.required]),
            arrivalPortId: new FormControl(null),
            carBodyNumber: new FormControl(null, [Validators.required]),
            carPrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
            landFreightPrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
            maritimeFreightPrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
            sellPrice: new FormControl(0, [Validators.required, CustomValidatorsService.invalidNumber]),
            arrivalDate: new FormControl(null),
            refranceNumber: new FormControl(null),
            carCost: new FormControl(null),
            netProfit: new FormControl(null),
        });

        this.isFormSubmitError = false;
        this.isRequestingToServer = false;


        this.form.controls.freightStateId.valueChanges.subscribe(value => { this.loadStateCities(value); this.updateFreightPrices() });
        this.form.controls.carSizeId.valueChanges.subscribe(value => this.updateFreightPrices());
        this.form.controls.cityId.valueChanges.subscribe(value => this.updateFreightPrices());
        this.disapledControls();
    }

    /**
     * Init Delete From
     */
    initDeletingForm() {
        this.form = new FormGroup({
            _id: new FormControl(),
            clinetName: new FormControl(null),
            refranceNumber: new FormControl(null),
        });
        this.form.disable();
    }

    /**
 * Close: Back To The Page
 */
    public closeModel() {
        this.isFormSubmitError = false;
        this.form.reset();
        this.router.navigate(['/sectorSales']);
    }


    /** Disapled Som Form Controls */
    disapledControls() {
        this.form.controls.landFreightPrice.disable();
        this.form.controls.maritimeFreightPrice.disable();
        this.form.controls.refranceNumber.disable();
        this.form.controls.carCost.disable();
        this.form.controls.netProfit.disable();
        
    }

    /**Get Items To Select From Them */
    getItems(itemName?: string): void {
        let itemsNames = itemName ? [itemName] : [
            "clinets", "freightCases", "buySources",
            "states", "ports"
        ];
        this.http.getSelectItems<any>(itemsNames).subscribe(res => {
            if (res.isSuccess) {
                itemsNames.forEach(itemName => {
                    this[itemName] = res.result[itemName];
                })
            } else {
                AlertService.showResponseMessage(res);
            }
        }, error => {
            AlertService.canRequestError(error);
        })
    }

    /**Reload Items */
    reloadItems(itemName: string): void {
        this.http.getSelectItems<any>(
            [itemName]).subscribe(res => {
                if (res.isSuccess) {
                    this[itemName] = res.result[itemName];
                }
                AlertService.showResponseMessage(res);
            }, error => {
                AlertService.canRequestError(error);
            })
    }


    /**
     *           
     */
    updateFreightPrices() {
        if (!this.form.controls.carSizeId.value || !this.form.controls.freightStateId.value) return;
        let state = this.states.find(st => st._id == this.form.controls.freightStateId.value);
        let city = state.cities.find(ci => ci._id == this.form.controls.cityId.value);
        //              
        this.form.controls.landFreightPrice.setValue(+city?.landFreightPrice || +state.fullLandFreightPriceToState);
        if (this.form.controls.carSizeId.value == CarSizesEnum.small)
            this.form.controls.maritimeFreightPrice.setValue(+state.maritimeFreightPriceToSmallCar);
        if (this.form.controls.carSizeId.value == CarSizesEnum.medium)
            this.form.controls.maritimeFreightPrice.setValue(+state.maritimeFreightPriceToMediumCar);
        if (this.form.controls.carSizeId.value == CarSizesEnum.big)
            this.form.controls.maritimeFreightPrice.setValue(+state.maritimeFreightPriceToBigCar);
    }

    /** Load State Cities */
    loadStateCities(stateId: string): void {
        if (!this.states) return;
        this.cities = this.states.find(m => m._id == stateId)?.cities
    }

}//End Class